import { Component, h, State, Event, EventEmitter } from '@stencil/core';

@Component({
  tag: 'at-login',
  styleUrl: 'login.css',
  shadow: false,
})
export class Login {
  @State() showPass = false;
  @State() usernameInputValid: boolean;
  @State() passwordInputValid: boolean;
  passInput: HTMLInputElement;
  userInput: HTMLInputElement;
  @State() Login = {
    username: '',
    password: '',
  };
  @Event({ bubbles: true, composed: true }) loginEvent: EventEmitter<any>;
  @Event({ bubbles: true, composed: true }) register: EventEmitter<any>;

  ToggleShowPass() {
    this.showPass = !this.showPass;
    alert;
  }

  onLogin(e: Event) {
    e.preventDefault();
    this.loginEvent.emit(this.Login);
    this.Login = {
      username: '',
      password: '',
    };
  }

  onRegister() {
    console.log('register');
    this.register.emit();
  }

  onUsernameInput(e: Event) {
    this.Login.username = (e.target as HTMLInputElement).value;
    if (this.Login.username.trim() !== '') {
      this.usernameInputValid = true;
    } else {
      this.usernameInputValid = false;
    }
  }
  onPasswordInput(e: Event) {
    this.Login.password = (e.target as HTMLInputElement).value;
    if (this.Login.password.trim() !== '') {
      this.passwordInputValid = true;
    } else {
      this.passwordInputValid = false;
    }
  }

  render() {
    let type = 'password';
    let icon = 'ft-eye';
    if (this.showPass) {
      type = 'text';
      icon = 'ft-eye-off';
    }

    return [
      <div class="app-content content">
        <div class="content-overlay"></div>
        <div class="content-wrapper">
          <div class="content-header row"></div>
          <div class="content-body">
            <section class="row flexbox-container">
              <div class="col-12 d-flex align-items-center justify-content-center">
                <div class="col-lg-4 col-md-8 col-10 box-shadow-2 p-0">
                  <div class="card border-grey border-lighten-3 m-0">
                    <div class="card-header border-0">
                      <div class="card-title text-center">
                        <div class="p-1">
                          <h1>Login</h1>
                        </div>
                      </div>
                    </div>
                    <div class="card-content">
                      <div class="card-body">
                        <form class="form-horizontal form-simple" novalidate onSubmit={this.onLogin.bind(this)}>
                          <fieldset class="form-group position-relative has-icon-left mb-1">
                            <input
                              type="text"
                              name="username"
                              id="username"
                              placeholder=" "
                              autocomplete="off"
                              ref={el => (this.userInput = el)}
                              value={this.Login.username}
                              onInput={this.onUsernameInput.bind(this)}
                              class="form-control-material username"
                              required
                            />
                            <div class="form-control-position">
                              <i class="la la-user"></i>
                            </div>
                          </fieldset>
                          <fieldset class="form-group position-relative">
                            <input
                              type={type}
                              name="password"
                              id="password"
                              placeholder=""
                              autocomplete="off"
                              class="form-control-material"
                              ref={el => (this.passInput = el)}
                              value={this.Login.password}
                              onInput={this.onPasswordInput.bind(this)}
                              required
                            />
                            <div typeof="button" class="form-control-position">
                              <i class={icon}></i>
                            </div>
                          </fieldset>
                          <div class="form-group row">
                            <div class="col-12 text-center text-sm-right">
                              <a class="card-link">Forgot Password?</a>
                            </div>
                          </div>
                          <button type="submit" class="btn btn-info btn-block">
                            <i class="ft-unlock"></i> Login
                          </button>
                        </form>
                      </div>
                    </div>
                    <div class="card-footer">
                      <div class="">
                        <p class="float-xl-right text-center m-0">
                          New here?
                          <a class="card-link" typeof="button" onClick={this.onRegister.bind(this)}>
                            Sign Up
                          </a>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>,
    ];
  }
}
